import { lazy, Suspense } from "react";
import Banner from "../components/banner/Banner";

const CoinsTable = lazy(() => import("../components/CoinsTable"));

const Homepage = () => {
  document.title = `Crypto Monster`;
  return (
    <>
      <Banner />
      <Suspense fallback={""}>
        <CoinsTable />
      </Suspense>
    </>
  );
};

export default Homepage;
